@import url("https://fonts.googleapis.com/css?family=Poppins:400,700");

/* ----- styleguide ----- */
:root {
  --black:
          rgba(0, 0, 0, 1);
  --celeste:
          rgba(205, 205, 205, 1);
  --red-2:
          rgba(221, 0, 0, 0.149);
  --red:
          rgba(221, 0, 0, 1);
  --x03-on-primary--high-emphasis:
          rgba(255, 255, 255, 1);

  --font-size-m: 14px;
  --font-size-l: 18px;
  --font-size-xl: 24px;

  --font-family-poppins: "Poppins";
}

.poppins-normal-black-24px {
  color: var(--black);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xl);
  font-weight: 400;
  font-style: normal;
}
.poppins-normal-black-14px {
  color: var(--black);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-m);
  font-weight: 400;
  font-style: normal;
}
.poppins-bold-red-18px {
  color: var(--red);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-l);
  font-weight: 700;
  font-style: normal;
}
.border-4px-celeste {
  border-width: 4px;
  border-style: solid;
  border-color: var(--celeste);
}
.poppins-bold-celeste-18px {
  color: var(--celeste);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-l);
  font-weight: 700;
  font-style: normal;
}
.navbar-header {
  color: var(--celeste);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-l);
  font-weight: 700;
  font-style: normal;
  cursor: pointer;
}

.border-4px-red {
  border-width: 4px;
  border-style: solid;
  border-color: var(--red);
}

.desktop-9 {
  align-items: flex-start;
  background-color: var(--x03-on-primary--high-emphasis);
  display: flex;
  width: 1440px;
}
/* ----- end styleguide ----- */

